import { Button, Input } from '@ya.praktikum/react-developer-burger-ui-components'
import { useState } from 'react';
import style from './ForgotPassword.module.css';
import {Link, useHistory} from 'react-router-dom';
import { forgotPassword } from '../../services/actions/auth';
import { useDispatch, useSelector } from '../../types/hooks';
import { PASSWORD_FORGOT } from '../../services/actions/forgotPassword';

export default function ForgotPassword() {

    const [valueForm, setValueForm] = useState({ email: '' })
    const history = useHistory();
    const dispatch = useDispatch();

    const onFormChange = (e: React.FormEvent<HTMLInputElement>) => {
        const formValue = e.currentTarget.value;
        const formName = e.currentTarget.name;
        setValueForm(prev => ({...prev, [formName]: formValue }))
    }

    const onFormSubmit = (e: React.SyntheticEvent) => {
        e.preventDefault();
        console.log(valueForm, "form");
        dispatch({ type: PASSWORD_FORGOT });
        dispatch(forgotPassword(valueForm));
        history.replace({ pathname: '/reset-password' });
    }


    return (
        <div className={style
    .main}>
            <p className="text text_type_main-large">
                 
            </p>

            <form onSubmit={onFormSubmit} className={style
            .form}>
                <Input
                    type={'text'}
                    placeholder={' e-mail'}
                    name={'email'}
                    onChange={onFormChange}
                    value={valueForm.email}
                    error={false}
                    errorText={''}
                    size={'default'}
                />

                <div className={style
                .formChild}>
                    <Button
                        type="primary"
                        size="large"
                    >
                        
                    </Button>
                </div>

            </form>

            <p className="text text_type_main-default text_color_inactive">
                 ? <Link to={{ pathname: '/login' }}></Link>
            </p>
        </div>
    )
}